.exhibitions {
  &__wrapper {
    position: relative;
  }

  &__article {
    @include on-desktop {
      @include page-grid;
      margin-bottom: 80px;
    }
  }

  &__article-content {
    grid-column: 9 /-1;

    @include on-desktop {
      margin-top: 150px;
    }
  }

  &__title {
    font-family: Montserrat, sans-serif;
    font-size: 36px;
    font-weight: 700;
    line-height: 36px;
    letter-spacing: 1.55px;
    text-align: center;

    @include on-tablet {
      font-size: 48px;
      line-height: 48px;
      text-align-last: left;
    }
  }

  &__side-mark {
    position: absolute;
    transform: rotate(-90deg);
    top: -170px;
    font-family: IBM Plex Sans, sans-serif;
    font-size: 14px;
    line-height: 14px;
    text-align: left;
    color: #687480;
    display: none;

    &::before {
      content: '';
      display: inline-block;
      width: 70px;
      height: 1px;
      background-color: #1a5a4c;
      margin-right: 20px;
      margin-bottom: 5px;
    }

    @include on-desktop {
      display: block;
    }
  }

  &__photo {
    margin-bottom: 20px;
    cursor: pointer;
    transition: 0.3s;

    &:hover {
      transform: scale(1.01);
      box-shadow: 4px 4px 6px #687480;
      transition: 0.3s;
    }

    @include on-desktop {
      grid-column: 1 /8;
    }
  }

  &__foto-mb {
    width: 100%;

    @include on-desktop {
      display: none;
    }
  }

  &__foto-ds {
    display: none;

    @include on-desktop {
      display: block;
      width: 100%;
    }
  }

  &__foto-2-mb {
    @include on-desktop {
      display: none;
    }
  }

  &__foto-2-ds {
    display: none;

    @include on-desktop {
      display: block;
    }
  }

  &__event {
    margin-right: 65px;
    font-family: IBM Plex Sans, sans-serif;
    font-size: 16px;
    font-weight: 500;
    line-height: 16px;
    text-align: left;
    color: #687480;

    @include on-tablet {
      font-size: 14px;
      line-height: 14px;
      margin-right: 30px;
    }
  }

  &__date {
    font-family: IBM Plex Sans, sans-serif;
    font-weight: 700;
    font-size: 16px;
    line-height: 24px;
    text-align: left;
    color: #1a5a4c;

    @include on-tablet {
      font-size: 14px;
      line-height: 21px;
    }
  }

  &__event-title {
    font-family: Montserrat, sans-serif;
    font-weight: 400;
    font-size: 28px;
    line-height: 28px;
    letter-spacing: 1.77px;
    text-align: left;
    color: #0f0e08;

    @include on-tablet {
      font-size: 36px;
      line-height: 36px;
    }

    &::after {
      content: '';
      background-image: url(../images/icons/title-after.png);
      background-repeat: no-repeat;
      display: inline-block;
      width: 10px;
      height: 10px;
      margin: 0 0 4px 12px;

      @include on-tablet {
        background-image: url(../images/icons/exhib-after-2.png);
        margin-left: 24px;
      }
    }
  }

  &__description {
    margin-bottom: 40px;
    font-family: IBM Plex Sans, sans-serif;
    font-size: 16px;
    line-height: 24px;
    text-align: left;
    color: #4e4e4e;

    @include on-tablet {
      width: 68%;
      margin-bottom: 60px;
    }

    @include on-desktop {
      width: 100%;
    }
  }
}
