.menu {
  position: fixed;
  top: 0;
  right: 0;
  width: 100%;
  opacity: 0;
  pointer-events: none;
  transition: 0.3s;

  &:target {
    opacity: 1;
    pointer-events: all;
  }

  &__container {
    @include on-tablet {
      display: flex;
      justify-content: space-around;
    }
  }

  &__content {
    margin-top: -20px;
    height: 424px;
    background-color: #1a5a4c;

    @include on-tablet {
      height: 235px;
    }
  }

  &__nav {
    width: 100%;
    border-bottom: 1px solid #fff;

    @include on-tablet {
      width: 42%;
      margin-top: 60px;
      border-bottom: none;
      border-right: 1px solid #fff;
    }

    @include on-desktop {
      width: 500px;
    }
  }

  &__close {
    background-image: url(../images/icons/close-menu.png);
    background-repeat: no-repeat;
    position: absolute;
    top: 27px;
    left: 20px;
    display: block;
    width: 24px;
    height: 24px;
    transition: 0.3s;

    &:hover {
      transform: scale(1.2);
      transition: 0.3s;
    }

    @include on-tablet {
      top: 36px;
      left: 38px;
    }

    @include on-desktop {
      left: 52px;
    }
  }

  &__links {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;

    @include on-tablet {
      margin: 0;
    }
  }

  &__item {
    list-style: none;
    width: 40%;
    margin-top: 32px;

    @include on-tablet {
      margin: 0;
      padding-bottom: 24px;
    }
  }

  &__link {
    font-family: IBM Plex Sans, sans-serif;
    font-size: 16px;
    font-weight: 500;
    line-height: 16px;
    text-align: left;
    text-decoration: none;
    color: #fff;
    transition: 0.3s;

    &:hover {
      padding-bottom: 4px;
      border-bottom: 1px solid #fff;
      transition: 0.3s;
    }
  }

  &__shedule {
    margin-top: 30px;

    @include on-tablet {
      margin-top: 40px;
    }

    @include on-desktop {
      margin-right: 60px;
    }
  }

  &__shedule-title {
    font-family: IBM Plex Sans, sans-serif;
    font-size: 14px;
    font-weight: 400;
    line-height: 19.69px;
    text-align: left;
    color: #fff;

    @include on-tablet {
      margin-bottom: 22px;
    }
  }

  &__shedule-time {
    font-family: IBM Plex Sans, sans-serif;
    font-size: 14px;
    font-weight: 600;
    line-height: 21px;
    text-align: left;
    color: #fff;
  }

  &__shedule-day {
    font-family: IBM Plex Sans, sans-serif;
    font-size: 14px;
    font-weight: 400;
    line-height: 21px;
    text-align: left;
    color: #fff;
  }

  &__shedule-2 {
    font-family: IBM Plex Sans, sans-serif;
    font-size: 14px;
    font-weight: 400;
    line-height: 21px;
    color: #fff;
    text-align: left;
    height: 63px;
    width: 245px;
    margin-top: 30px;

    @include on-tablet {
      height: 105px;
      width: 143px;
      margin: 104px -12px 0 0;
    }

    @include on-desktop {
      width: 168px;
      margin: 104px 0 0 -140px;
    }
  }

  &__shedule-2-word-1 {
    font-weight: 600;
  }

  &__bottom {
    height: 100vh;
    background-color: #000;
    opacity: 0.5;
  }
}
