.gallery {
  margin-bottom: 80px;

  @include on-desktop {
    margin-bottom: 200px;
  }

  &__title {
    font-family: Montserrat, sans-serif;
    font-size: 36px;
    line-height: 36px;
    text-align: center;

    @include on-tablet {
      font-size: 48px;
      line-height: 48px;
      text-align: left;
    }
  }

  &__photos-mb {
    display: flex;

    @include on-tablet {
      display: none;
    }
  }

  &__photo-1-mb {
    margin-right: 16px;
  }

  &__photo-2-mb {
    overflow: hidden;
    margin-right: -20px;
  }

  &__foto-ds {
    width: 100%;
    cursor: pointer;
    transition: 0.3s;

    &:hover {
      transform: scale(1.01);
      box-shadow: 4px 4px 6px #687480;
      transition: 0.3s;
    }
  }

  &__photos-tab {
    display: none;

    @include on-tablet {
      display: flex;
    }
    @include on-desktop {
      display: none;
    }
  }

  &__photo-1-tab {
    margin-right: 18px;
  }

  &__photo-2-tab {
    margin-right: 18px;
  }

  &__photo-3-tab {
    overflow: hidden;
    margin-right: -38px;
  }

  &__photos-ds {
    display: none;

    @include on-desktop {
      display: block;
      @include page-grid;
    }
  }

  &__photo-1-ds {
    grid-column: 1 /5;
  }

  &__photo-2-ds {
    grid-column: 5 /-1;
    margin-bottom: 28px;
  }

  &__photo-3-ds {
    grid-column: 1 /9;
  }

  &__photo-4-ds {
    grid-column: 9 /-1;
  }

  &__slider-buttons {
    background-image: url(../images/icons/slider-butt.png);
    background-repeat: no-repeat;
    display: block;
    width: 104px;
    height: 12px;
    margin: 20px auto;

    @include on-desktop {
      display: none;
    }
  }
}
