.themes {

  &__photo {
    margin-inline: -20px;

    @include on-tablet {
      margin: 0;
    }

    @include on-desktop {
      margin-inline: -55px;
    }
  }

  &__foto--mb {
    width: 100%;
    height: 210px;

    @include on-tablet {
      display: none;
    }
  }

  &__foto--tab {
    display: none;

    @include on-tablet {
      display: block;
      width: 100%;
      height: 390px;
    }

    @include on-desktop {
      display: none;
    }
  }

  &__foto--ds {
    display: none;

    @include on-desktop {
      display: block;
      width: 100%;
      height: 550px;
    }
  }

  &__content {
    position: relative;
    padding-top: 20px;
    margin: 0 auto;
    top: -60px;
    width: 280px;
    background-color: #eee;
    text-align: center;
    border-bottom: 2px solid #c4c2c2;

    @include on-tablet {
      top: -80px;
      width: 460px;
      padding: 34px 40px;
      text-align: left;
    }

    @include on-desktop {
      width: 430px;
      padding: 34px 70px;
    }
  }

  &__event {
    margin-right: 20px;
    font-family: IBM Plex Sans, sans-serif;
    font-size: 16px;
    font-weight: 500;
    line-height: 16px;
    text-align: left;
    color: #687480;

    @include on-tablet {
      margin-right: 180px;
      font-size: 14px;
      line-height: 14px;
    }
  }

  &__date {
    font-family: IBM Plex Sans, sans-serif;
    font-size: 16px;
    font-weight: 700;
    line-height: 24px;
    text-align: left;
    color: #1a5a4c;

    @include on-tablet {
      font-size: 14px;
      line-height: 21px;
    }
  }

  &__title {
    margin: 20px 0;
    font-family: Montserrat, sans-serif;
    font-size: 28px;
    font-weight: 400;
    line-height: 28px;
    letter-spacing: 1.55px;
    text-align: center;
    color: #0f0e08;

    @include on-tablet {
      font-size: 36px;
      line-height: 36px;
      text-align: left;
    }
  }
}
