.header {
  margin-bottom: 100px;

  &__wrapper {
    position: relative;

    @include on-tablet {
      @include page-grid;
    }
  }

  &__link {
    background-image: url(../images/icons/menu-icon.png);
    background-repeat: no-repeat;
    position: absolute;
    top: 30px;
    display: inline-block;
    width: 24px;
    height: 24px;
    transition: 0.3s;

    &:hover {
      transform: scale(1.1);
      transition: 0.3s;
    }

    @include on-tablet {
      grid-column: 1 /2;
    }
  }

  &__logo {
    background-image: url(../images/icons/NAMU.svg);
    background-repeat: no-repeat;
    position: absolute;
    top: 20px;
    margin-left: 40%;
    display: inline-block;
    width: 55px;
    height: 60px;
    transition: 0.3s;

    &:hover {
      transform: scale(1.05);
      transition: 0.3s;
    }

    @include on-tablet {
      grid-column: 4 /6;
      background-image: url(../images/icons/header-logo.svg);
      width: 220px;
      height: 70px;
      margin-left: 37%;
    }

    @include on-desktop {
      grid-column: 6 /8;
    }
  }

  &__photo {
   margin-inline: -20px;

   @include on-tablet {
    grid-column: 4 /-1;
    margin: 0 -38px 0 0;
   }

   @include on-desktop {
    grid-column: 6 /-1;
   }
  }

  &__foto--mb {
    width: 100%;

    @include on-tablet {
      display: none;
    }
  }

  &__foto--tab {
    display: none;

    @include on-tablet {
      display: block;
      width: 100%;
      height: 384px;
    }

    @include on-desktop {
      display: none;
    }
  }

  &__foto--desk {
    display: none;

    @include on-desktop {
      display: block;
      width: 100%;
    }
  }

  &__title {
    font-family: Montserrat, sans-serif;
    font-size: 45px;
    font-weight: 700;
    line-height: 45px;
    text-align: center;

    @include on-tablet {
      grid-column: 1 /4;
      grid-row: 1;
      text-align: left;
      margin-top: 120px;
      font-size: 54px;
      line-height: 65px;
    }

    @include on-desktop {
      grid-column: 1 /6;
      margin-top: 200px;
      font-size: 72px;
      line-height: 86px;
    }
  }

  &__button {
    display: flex;
    text-align: center;
    align-items: center;
    justify-content: space-evenly;
    width: 98%;
    height: 50px;
    border: 1px solid #1a5a4c;
    font-family: IBM Plex Sans, sans-serif;
    font-size: 16px;
    font-weight: 500;
    line-height: 16px;
    cursor: pointer;
    transition: 0.3s;

    &:hover {
      background-color: #dbd7d7;
      transition: 0.3s;
    }

    @include on-tablet {
      grid-column: 1 /4;
      width: 270px;
      height: 70px;
      margin-top: -80px;
    }

    @include on-desktop {
      grid-column: 3 /6;
      margin-top: -180px;
    }

    &::after {
      background-image: url(../images/icons/button-arrow.png);
      background-repeat: no-repeat;
      background-position: center;
      content: '';
      display:inline-block ;
      width: 50px;
      height: 50px;
      background-color: #1a5a4c;
      margin-right: -43%;

      @include on-tablet {
        width: 70px;
        height: 70px;
        margin-right: -32%;
      }
    }
  }
}
