.footer {
  &__wrapper {
    @include page-grid;
    position: relative;
    padding-bottom: 50px;

    @include on-tablet {
      padding-bottom: 30px;
    }
  }

  &__logo {
    grid-column: 1 /2;

    @include on-tablet {
      position: absolute;
      left: 0;
      top: 24px;
    }
  }

  &__logo-icon {
    transition: 0.3s;

    &:hover {
      transform: scale(1.1);
      transition: 0.3s;
    }
  }

  &__icons {
    grid-column: 2 /-1;
    text-align: right;
    padding-top: 20px;

    @include on-tablet {
      grid-column: 1 /2;
      text-align: left;
      padding-top: 148px;
    }
  }

  &__facebook {
    text-decoration: none;
    margin-right: 20px;

    @include on-tablet {
      margin-right: 24px;
    }

    @include on-desktop {
      margin-right: 16px;
    }
  }

  &__facebook-icon {
    transition: 0.3s;

    &:hover {
      transform: scale(1.2);
      transition: 0.3s;
    }
  }

  &__instagram-icon {
    transition: 0.3s;

    &:hover {
      transform: scale(1.2);
      transition: 0.3s;
    }
  }

  &__shedule {
    grid-column: 1 /2;
    width: 140px;;

    @include on-tablet {
      grid-column: 2 /4;
      width: 220px;
    }

    @include on-desktop {
      grid-column: 3 /5;
      width: 170px;
    }
  }

  &__shedule-title {
    font-family: IBM Plex Sans, sans-serif;
    font-weight: 400;
    font-size: 14px;
    line-height: 19.69px;
    text-align: left;
    color: #0f0e08;
  }

  &__shedule-time {
    font-family: IBM Plex Sans, sans-serif;
    font-size: 14px;
    font-weight: 600;
    line-height: 21px;
    text-align: left;

    @include on-tablet {
      display: inline-block;
    }
  }

  &__shedule-day, &__shedule-note {
    font-family: IBM Plex Sans, sans-serif;
    font-size: 14px;
    font-weight: 400;
    line-height: 21px;
    text-align: left;
  }

  &__shedule-note {
    @include on-tablet {
      padding-right: 30px;
    }

    @include on-desktop {
      padding: 0;
    }
  }

  &__shedule-word-1 {
    @include on-desktop {
      font-weight: 600;
    }
  }

  &__shedule-colon {
    display: none;

    @include on-desktop {
      display: inline;
    }
  }

  &__shedule-word-2 {
    display: block;

    @include on-tablet {
      display: inline;
    }

    @include on-desktop {
      display: block;
    }
  }

  &__shedule-word-3 {
    @include on-tablet {
      display: block;
    }

    @include on-desktop {
      display: inline;
    }
  }

  &__shedule-word-4 {
    @include on-desktop {
      display: block;
    }
  }

  &__contacts-title {
    font-family: IBM Plex Sans, sans-serif;
    font-weight: 400;
    font-size: 14px;
    line-height: 19.69px;
    text-align: left;
    color: #0f0e08;
  }

  &__contacts {
    grid-column: 2 /-1;
    width: 120px;

    @include on-tablet {
      grid-column: 4 /6;
      width: 220px;
    }

    @include on-desktop {
      grid-column: 5 /7;
      width: 170px;
    }
  }

  &__links {
    list-style: none;
    padding: 0;
  }

  &__link {
    text-decoration: none;
    font-family: IBM Plex Sans, sans-serif;
    font-size: 14px;
    font-weight: 400;
    line-height: 21px;
    text-align: left;
    color: #0f0e08;
    transition: 0.3s;

    &:hover {
      color: #746f59;
      border-bottom: 1px solid #746f59;
      transition: 0.3s;
    }
  }

  &__mark {
    position: absolute;
    right: 0;
    bottom: 310px;
    transition: 0.3s;

    &:hover {
      transform: scale(1.1);
      transition: 0.3s;
    }

    @include on-tablet {
      display: none;
    }
  }

  &__line {
    grid-column: 1 /-1;
    margin: 70px 0 40px;
    height: 1px;
    width: 100%;
    background-color: #1a5a4c;

    @include on-tablet {
      grid-column: 2 /6;
      margin: 60px 0;
    }

    @include on-desktop {
      position: absolute;
      transform: rotate(90deg);
      width: 260px;
      top: 150px;
      left: 107%;
      margin: 0;
    }
  }

  &__nav-1 {
    display: flex;
    flex-direction: column;

    @include on-tablet {
      grid-column: 2 /4;
    }

    @include on-desktop {
      grid-column: 8 /10;
      margin-top: 20px;
    }
  }

  &__nav-2 {
    display: flex;
    flex-direction: column;

    @include on-desktop {
      margin-top: 20px;
    }
  }

  &__nav-links {
    padding-bottom: 20px;
  }

  &__nav-link {
    text-decoration: none;
    font-family: IBM Plex Sans, sans-serif;
    font-size: 14px;
    font-weight: 400;
    line-height: 14px;
    text-align: left;
    color: #0f0e08;
    transition: 0.3s;

    &:hover {
      border-bottom: 1px solid #0f0e08;
      padding-bottom: 4px;
      width: fit-content;
      transition: 0.3s;
    }
  }

  &__note {
    text-align: center;
    font-family: IBM Plex Sans, sans-serif;
    font-size: 12px;
    font-weight: 300;
    line-height: 16.88px;
    text-align: center;
    color: #0f0e08;
  }

  &__note-1 {
    grid-column: 1 /-1;
    margin: 80px 0 15px;

    @include on-tablet {
      grid-column: 5 /-1;
      margin: 170px 0 24px;
      text-align: right;
    }

    @include on-desktop {
      margin-top: 110px;
    }
  }

  &__note-2 {
    grid-column: 1 /-1;

    @include on-tablet {
      grid-column: 1 /3;
      margin-top: -40px;
    }

    @include on-desktop {
      grid-column: 1 /4;
    }
  }
}
