.subscription {
  position: relative;
  margin-bottom: 50px;

  &__foto-mb {
    width: 100%;

    @include on-tablet {
      display: none;
    }
  }

  &__foto-tab {
    display: none;

    @include on-tablet {
      display: block;
      width: 100%;
    }
  }

  &__content {
    position: absolute;
    top: 15%;
    width: 100%;

    @include on-tablet {
      top: 17%;
    }

    @include on-desktop {
      top: 30%;
    }
  }

  &__title {
    font-family: Montserrat, sans-serif;
    font-size: 36px;
    font-weight: 700;
    line-height: 36px;
    letter-spacing: 3px;
    color: #fff;
    text-align: center;

    @include on-tablet {
      margin: 20px;
      font-size: 48px;
      line-height: 48px;
    }
  }

  &__text {
    font-family: IBM Plex Sans, sans-serif;
    font-size: 18px;
    line-height: 27px;
    color: #fff;
    text-align: center;
    width: 80%;
    margin: 0 auto;

    @include on-tablet {
      line-height: 18px;
    }
  }

  &__form {
    margin-top: 50px;

    @include on-tablet {
      margin-top: 56px;
    }
  }

  &__inputs {
    display: flex;
    justify-content: center;
  }

  &__input {
    height: 50px;
    width: 230px;
    border: none;
    outline: none;
    box-sizing: border-box;
    padding-left: 24px;
    cursor: pointer;
    transition: 0.3s;

    &:hover {
      background-color: #d3d3d3;
      transition: 0.3s;
    }

    @include on-tablet {
      width: 380px;
      height: 70px;
      margin-right: 10px;
    }

    @include on-desktop {
      width: 470px;
      margin-right: 30px;
    }
  }

  &__button {
    width: 50px;
    height: 50px;
    background-color: #f14a27;
    border: none;
    cursor: pointer;
    transition: 0.3s;

    &:hover {
      transform: scale(1.1);
      transition: 0.3s;
    }

    @include on-tablet {
      width: 70px;
      height: 70px;
    }
  }
}
